.feed {
  flex: 1;
  /* display: flex; */
  justify-content: center;
  /* align-items: center; */
}
/* post component styling */
.post {
  display: flex;
  flex-direction: column;
  margin-top: 30px;
  width: 100%;
  background-color: #fff;
  border-radius: 20px;
  box-shadow: 0px 5px 7px -7px rgba(0, 0, 0, 0.75);
}
.post__container {
  display: flex;
  border-bottom: 1px solid #eff2f5;
  padding: 13px;
}
.post__container > form {
  display: flex;
  flex: 1;
}
.post__container > form > button {
  display: none;
}
.post__container > form > input {
  outline-width: 0;
  border: none;
  border-radius: 99px;
  background-color: #eff2f5;
  padding: 5px 20px;
  margin: 0px 10px;
  font-size: 17px;
}
.post__container > form > button {
  display: none;
}
.post__input {
  flex: 1;
}
.post__floot,
.postItem__options {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}
.post__option,
.postItem__option {
  /* flex-grow: 1; */
  display: flex;
  padding: 15px;
  margin: 5px;
  color: gray;
  cursor: pointer;
}
.post__option:hover,
.postItem__option:hover {
  background-color: #eff2f5;
  border-radius: 20px;
}
.postItem__option > p {
  padding: 5px;
}
.postItem {
  width: 100%;
  margin-top: 15px;
  background-color: #fff;
  border-radius: 20px;
  box-shadow: 0px 5px 7px -7px rgba(0, 0, 0, 0.75);
}
/* post item component styling  */
.postItem__body {
  display: flex;
  margin: 5px;
  padding: 13px;
}
.postItem__info {
  margin-left: 10px;
}
.postItem__info > p,
.postItem__name > p {
  font-size: small;
  color: gray;
}
.postItem__name {
  display: flex;
}
.postItem__image {
  max-width: 100%;
  max-height: 100%;
}
.postItem__content {
  margin: 0 0 10px 15px;
  padding: 7px;
}

.postItem__delete {
  margin: 0 0 0 auto;
}
.postItem__delete :hover {
  background-color: #eff2f5;
  border-radius: 20px;
  padding: 2px;
  color: rgb(66, 13, 13);
  cursor: pointer;
}
